
import type { IdTokenClaims, OidcClientSettings, User } from "oidc-client-ts";
import type { RouteLocationNormalized } from "vue-router";


export interface StoreOidcListeners {
  userLoaded?: (user: User) => void;
  userUnloaded?: () => void;
  accessTokenExpiring?: () => void;
  accessTokenExpired?: () => void;
  silentRenewError?: () => void;
  userSignedOut?: () => void;
  oidcError?: (payload?: PayloadType) => void;
  automaticSilentRenewError?: (payload?: PayloadType) => void;
  [key: string]: any;
}

export interface OidcSigninSilentOptions { }
export interface OidcSigninPopupOptions { }

export interface OidcSigninRedirectOptions {
  useReplaceToNavigate?: boolean;
  skipUserInfo?: boolean;
  extraQueryParams?: Record<string, any>;
}

export interface StoreOidcClientSettings extends OidcClientSettings {

  loginHint?: string;
  popupRedirectUri?: string;
  silentRedirectUri?: string;
  automaticSilentRenew?: boolean;
  automaticSilentSignin?: boolean;
  extraQueryParams?: Record<string, any>;
  [key: string]: any
}

export interface OidcStoreSettings {
  dispatchEventsOnWindow?: boolean;
  isPublicRoute?: (route: RouteLocationNormalized) => boolean;
  publicRoutePaths?: string[];
  routeBase?: string;
  defaultSigninRedirectOptions?: OidcSigninRedirectOptions;
  defaultSigninSilentOptions?: OidcSigninSilentOptions;
  defaultSigninPopupOptions?: OidcSigninPopupOptions;
  isAuthenticatedBy?: "access_token" | "id_token";
}

export interface OidcStoreState {
  access_token: string | null;
  id_token?: string | null;
  refresh_token?: string | null;
  user: IdTokenClaims | null;
  scopes: string[] | null;
  is_checked: boolean;
  events_are_bound: boolean;
  error: string | null;
}

export interface OidcStoreGetters {
  oidcIsAuthenticated: any | boolean;
  oidcUser: any | null;
  oidcAccesstoken: string | string | null;
  oidcAccessTokenExp: any | number | null;
  oidcScopes: any | string[] | null;
  oidcIdtoken: string | string | null;
  oidcIdTokenExp: any | number | null;
  oidcAuthenticationIsChecked: any | boolean | null;
  oidcError: any | string | null;
  oidcIsRoutePublic: any | boolean;
  oidcRefreshtoken: string | string | null;
  oidcRefreshTokenExp: any | number | null;
}

export interface OidcStoreActions {
  oidcCheckAccess: (route: RouteLocationNormalized) => Promise<boolean>;
  authenticateOidc: (payload?: PayloadType) => void;
  authenticateOidcSilent: (payload?: PayloadType) => Promise<User | null>;
  authenticateOidcPopup: (payload?: PayloadType) => Promise<void>;
  oidcSignInCallback: (url?: string) => Promise<string>;
  oidcSignInPopupCallback: (url?: string) => Promise<User | undefined>;
  oidcWasAuthenticated: (user: User | null) => void;
  getUser: () => Promise<User | null>;
  addOidcEventListener: (payload: PayloadType) => void;
  removeOidcEventListener: (payload: PayloadType) => void;
  signOutOidc: (payload?: object) => void;
  signOutOidcCallback: () => void;
  signOutPopupOidc: (payload?: object) => void;
  signOutPopupOidcCallback: () => void;
  signOutOidcSilent: (payload?: object) => Promise<void>;
  storeUser: (user: User | null) => void;
  removeUser: () => void;
  removeUser: () => void;
  clearStaleState: () => void;
}
export interface OidcStoreMutations {
  setOidcAuth: (user: User) => void;
  setUser: (user: User) => void;
  unsetOidcAuth: () => void;
  setOidcAuthIsChecked: () => void;
  setOidcEventsAreBound: () => void;
  setOidcError: (err: PayloadType | string | null) => void;
}

export type OidcStore = OidcStoreState & OidcStoreGetters & OidcStoreActions & OidcStoreMutations
export type PayloadType = string | { [key: string]: any }
